//   [] [] []      []     []  [] []       []  [] []   
//

export const letterConvert = (letter: string): string => {
    if (letter === "") { return "e" }
    if (letter === "") { return "o" }
    if (letter === "") { return "u" }
    if (letter === "") { return "y" }
    if (letter === "") { return "" }
    if (letter === "") { return "a" }
    if (letter === "") { return "" }
    if (letter === "") { return "x" }
    if (letter === "") { return "c" }
    if (letter === "") { return "" }

    return letter
}
